<template>
  <div class="regist">
    <div class="text-center">
      <h1 class="underline text-center">Q&A 목록</h1>
    </div>
    <div>
      <div class="m-3 row justify-content-end">
        <form class="form-inline" action="${root}/notice/">
          <b-form-select v-model="key" class="form-control">
            <option value="">선택</option>
            <option value="userid">아이디</option>
            <option value="title">제목</option>
            <option value="articleno">글번호</option>
          </b-form-select>
          <b-form-input type="text" class="ml-1 form-control" v-model="word" />
          <b-button
            @click.prevent="search"
            class="btnsearch ml-1"
            variant="primary"
          >
            검색
          </b-button>
        </form>
      </div>
    </div>
    <div style="text-align: right">
      <b-button @click="movePage" variant="outline-primary">글 등록</b-button>
    </div>
    <div v-if="articles.length">
      <table id="book-list">
        <colgroup>
          <col style="width: 5%" />
          <col style="width: 45%" />
          <col style="width: 25%" />
          <col style="width: 25%" />
        </colgroup>
        <thead>
          <tr>
            <th>글번호</th>
            <th>제목</th>
            <th>작성자</th>
            <th>작성일</th>
          </tr>
        </thead>
        <tbody>
          <qnA-list-item
            v-for="article in articles"
            :key="article.articleno"
            :article="article"
          ></qnA-list-item>
        </tbody>
      </table>
      <board-pagination
        :searchKey="key"
        :word="word"
        :changed="changed"
        @pagechanged="pagechanged"
      ></board-pagination>
    </div>
    <div v-else class="text-center">검색 결과가 없습니다.</div>
  </div>
</template>

<script>
import http from "@/api/http.js";
import QnAListItem from "@/components/QnA/QnAListItem.vue";
import BoardPagination from "@/components/BoardPagination.vue";
export default {
  components: {
    QnAListItem,
    BoardPagination,
  },
  data() {
    return {
      pg: 1,
      key: "",
      word: "",
      changed: false,
      articles: [],
    };
  },
  created() {
    http
      .get(`/board?pg=1&key=${this.key}&word=${this.word}`)
      .then(({ data }) => {
        this.articles = data;
      });
  },
  methods: {
    movePage() {
      this.$router.push("/qna/write");
    },
    search() {
      this.changed = !this.changed;
      http
        .get(`/board?pg=1&key=${this.key}&word=${this.word}`)
        .then(({ data }) => {
          this.articles = data;
        });
    },
    pagechanged() {
      this.pg = this.$route.query.pg;
      http
        .get(`/board?pg=${this.pg}&key=${this.key}&word=${this.word}`)
        .then(({ data }) => {
          this.articles = data;
        });
    },
  },
};
</script>

<style scoped>
.btnsearch {
  width: 70px;
}
</style>
