<template>
  <div class="regist">
    <div class="text-center">
      <h1 class="underline text-center">Q&A 상세보기</h1>
    </div>
    <div class="regist_form">
      <label for="articleno">글번호</label>
      <div class="view">{{ article.articleno }}</div>
      <label for="subject">제목</label>
      <div class="view">{{ article.subject }}</div>
      <label for="userid">작성자</label>
      <div class="view">{{ article.userid }}</div>
      <label for="price">작성일</label>
      <div class="view">{{ article.regtime }}</div>
      <label for="content">내용</label>
      <div class="view" v-html="article.content"></div>
      <div style="padding-top: 15px">
        <router-link
          :to="{ name: 'modify', params: { articleno: article.articleno } }"
          ><b-button variant="primary">수정</b-button></router-link
        >
        <router-link
          :to="{ name: 'delete', params: { articleno: article.articleno } }"
          ><b-button variant="danger">삭제</b-button></router-link
        >
        <router-link to="/qna/list"
          ><b-button variant="secondary">목록</b-button></router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/api/http.js";
export default {
  data: function () {
    return {
      article: {},
    };
  },
  created() {
    http.get(`/board/${this.$route.params.articleno}`).then(({ data }) => {
      this.article = data;
      console.log("글보기 완료!!!");
    });
  },
};
</script>

